  padding: 5px 0 0;
  margin: 15px 0;
  align-items: center;
}

.dashboard p {
  flex-basis: 0;
  width: auto;
  margin: 0;
  flex-grow: 1;
  padding: 5px 0px;
  text-align: center;
  
}

.result {
  font-size: 1.5rem;
  text-align: center;
}